@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
:root {
	--primary-font: 'Poppins', sans-serif;
}

html {
	scroll-behavior: smooth;
}

.navbar--container {
	background: #f1f6f9;
}

.navbar {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	font-family: 'Poppins', sans-serif;
	margin: auto 5%;
	padding-top: 1%;
}

.navbar > .navbar--logo {
	font-size: 36px;
	color: #3f3f3f;
	cursor: pointer;
	padding-top: 1%;
}

.navbar--items > .item {
	margin: auto 10px;
	color: #3f3f3f;
	cursor: pointer;
	font-size: 18px;
}

@media only screen and (max-width: 600px) {
	.navbar > .navbar--logo {
		font-size: 24px;
	}

	.navbar--items > .item {
		font-size: 14px;
	}
}

.banner--container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 90vh;
	background: #f1f6f9;
	-webkit-clip-path: inset(0 0 0 0 round 0 0 20% 0);
	        clip-path: inset(0 0 0 0 round 0 0 20% 0);
}

.container--title {
	color: #3f3f3f; /* find a better color */
	font-size: 64px;
	font-family: var(--primary-font);
	letter-spacing: 2px;
	padding-top: 10%;
}

.container--subtitle {
	font-size: 24px;
	color: #3f3f3f;
	font-family: var(--primary-font);
	letter-spacing: 1px;
}

.container--signup-box {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: row;
	margin-top: 2%;
}

.signup-box--inputbox {
	width: 70%;
	padding: 1%;
	border-radius: 10px;
	border: solid 2px #3f3f3f;
	outline: none;
	height: 40px;
}

::-moz-placeholder {
	font-family: var(--primary-font);
	text-align: center;
	font-size: 16px;
	opacity: 0.4;
}

.signup-box--inputbox[type='email'] {
	font-family: var(--primary-font);
	color: #3f3f3f;
	font-size: 16px;
}

.signup-box--button {
	padding: 2%;
	border-radius: 10px;
	border: 1px solid #3f3f3f;
	outline: none;
	font-family: var(--primary-font);
	font-size: 16px;
	background: #9ca6d9;
	color: #fffeee;
	cursor: pointer;
}

.signup-box--button:active {
	font-size: 15.5px;
}

.carbonPic {
	height: 350px;
	width: auto;
	border-radius: 25px;
}

.comment-container--title > h1 {
	font-family: var(--primary-font);
	padding-bottom: 20px;
}

@media only screen and (max-width: 600px) {
	.container--title {
		font-size: 48px;
	}

	.container--subtitle {
		font-size: 18px;
	}
}

.ui.list {
	display: flex;
	justify-content: center;
	align-items: center;
	font-family: var(--primary-font);
}

.comment-container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 3% auto;
}

.comment-container--image {
	border-radius: 50%;
	height: 70px;
}

.content {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	margin: 0% 25%;
}

.content--name {
	color: #233eee;
	font-size: 18px;
}

.content--title {
	font-family: var(--primary-font);
	font-size: 14px;
	padding: 10px;
}

.ui.card {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: column;
}

.pricing--card {
	border: 1px solid #3f3f3f;
	border-radius: 20px;
	margin: 2%;
	padding: 50px 20px;
	cursor: pointer;
}

.pricing--card:hover {
	background: #d3dbff;
	box-shadow: 2px 2px 2px #3f3f3f;
}

.card-pricing--title {
	text-align: center;
	font-size: 48px;
	border-bottom: 1px solid #3f3f3f;
}

.pricing-benefits > li {
	list-style-type: none;
	padding: 3px;
}

.comment-container--title {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding-top: 5%;
	padding-bottom: 5%;
}

.container--section-pricing {
	background: #f1f3f8;
	/* background: #d3dbff; */
	padding-top: 5%;
	padding-bottom: 5%;
	-webkit-clip-path: inset(0 0 0 0 round 20% 0 20% 0);
	        clip-path: inset(0 0 0 0 round 20% 0 20% 0);
}

.container--section-pricing > h1 {
	text-align: center;
	font-family: var(--primary-font);
	font-size: 36px;
}

.container {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	margin: 5%;
}

@media only screen and (max-width: 600px) {
	.container {
		flex-direction: column;
	}
}

.mapContainer {
	padding-top: 75px;
	display: flex;
	align-items: flex-start;
	justify-content: center;
}

.google-map {
	height: 40vh;
}

.title {
	font-size: 36px;
	text-align: center;
	font-family: var(--primary-font);
	padding-top: 100px;
}

.footer-container {
	padding-top: 100px;
	height: auto;
	width: 100%;
	background: #f1f6f9;
	display: flex;
	justify-content: center;
	align-items: center;
}

.footer-items {
	display: flex;
	justify-content: space-evenly;
	align-items: center;
	flex-direction: row;
}

.footer-links {
	font-size: 18px;
	list-style-type: none;
	margin: auto 50px;
	font-family: var(--primary-font);
}

@media only screen and (max-width: 600px) {
	.footer-links {
		margin: auto 25px;
		font-size: 14px;
	}
}

@media only screen and (max-width: 720px) {
	/* Banner section */
	.banner--container {
		margin-bottom: 15%;
	}

	/* Pricing section */
	.container {
		flex-direction: column;
	}

	.container--section-pricing {
		margin-top: 15%;
		margin-bottom: 15%;
	}
}

@media only screen and (max-width: 605px) {
	/* Banner */
	.container--title {
		padding-top: 20%;
		font-size: 3.5rem;
	}

	.container--subtitle {
		font-size: 1.5rem;
	}

	.carbonPic {
		height: 300px;
	}
}

@media only screen and (max-width: 536px) {
	/* Banner */
	.banner--container {
		height: 80vh;
	}

	.container--subtitle {
		font-size: 1rem;
		margin-top: 2rem;
	}

	.container--signup-box {
		margin-bottom: 0.5rem;
	}

	/* Testimonials */
	.section--title,
	.container--section-pricing > h1 {
		font-size: 1.5rem;
	}

	.comment-container {
		margin: 5% auto;
	}

	.content--description {
		font-size: 0.8rem;
	}

	/* Pricing */
	.card-pricing--title {
		font-size: 1.8rem;
	}

	.pricing--card {
		font-size: 0.8rem;
		margin: 1% auto;
		padding-left: 0;
		padding: 10px auto;
	}

	.title {
		font-size: 1.8rem;
	}
}

