@media only screen and (max-width: 720px) {
	/* Banner section */
	.banner--container {
		margin-bottom: 15%;
	}

	/* Pricing section */
	.container {
		flex-direction: column;
	}

	.container--section-pricing {
		margin-top: 15%;
		margin-bottom: 15%;
	}
}

@media only screen and (max-width: 605px) {
	/* Banner */
	.container--title {
		padding-top: 20%;
		font-size: 3.5rem;
	}

	.container--subtitle {
		font-size: 1.5rem;
	}

	.carbonPic {
		height: 300px;
	}
}

@media only screen and (max-width: 536px) {
	/* Banner */
	.banner--container {
		height: 80vh;
	}

	.container--subtitle {
		font-size: 1rem;
		margin-top: 2rem;
	}

	.container--signup-box {
		margin-bottom: 0.5rem;
	}

	/* Testimonials */
	.section--title,
	.container--section-pricing > h1 {
		font-size: 1.5rem;
	}

	.comment-container {
		margin: 5% auto;
	}

	.content--description {
		font-size: 0.8rem;
	}

	/* Pricing */
	.card-pricing--title {
		font-size: 1.8rem;
	}

	.pricing--card {
		font-size: 0.8rem;
		margin: 1% auto;
		padding-left: 0;
		padding: 10px auto;
	}

	.title {
		font-size: 1.8rem;
	}
}
